.navbar {
  margin: 0;
  padding: 0;
  height: 7vh;
  background-color: #2222224f;
}

.navbar .container {
  max-width: 95%;
  height: 100%;
  margin: 0;
  padding: 0;
  margin: auto;
  display: flex;
  justify-content: flex-start;
  align-content: center;
  margin: 0 auto;
}

.navbar .container .list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin: auto 0;
  font-family: 'Righteous', cursive;
  display: flex;
  flex-direction: row;
}

.navbar .container .list .list_item {
  font-size: 30px;
  font-weight: 400;
  text-decoration: none;
  color: #cfcfcf;
  border-bottom: 1px solid #ffffffab;
  padding: 3px 5px;
  transition: 0.1s ease;
  background-color: transparent;
}

.navbar .container .list .list_item:hover {
  border-width: 2px;
  color: white;
}
