.hex-value {
  position: relative;
  bottom: 12%;
  right: 8%;
  transform: rotate(-90deg);
  margin: auto;
  color: white;
  float: right;
  border: 1px solid rgba(36, 36, 36, 0);
  background-color: rgba(12, 12, 12, 0.219);
  border-radius: 5px;
  font-size: 14px;
  box-shadow: 5px 5px 10px rgba(2, 2, 2, 0.562);
  transition: 0.1s ease-out;
}

.hex-value:hover {
  bottom: 19%;
  font-size: 20px;
  transform: rotate(0deg);
  background-color: rgba(236, 236, 236, 0.767);
  color: rgba(0, 0, 0, 0.836);
}
.style_continue{
  height: 90%;
  left: 1%;
  margin: auto 0;
  position: relative;
  display: block;
  align-items: left;
}

@media only screen and (max-width:600px){
  .hex-value {
    position: relative;
    top: 45%;
    left: 0%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    transform: rotate(-90deg);
    margin: auto;
    padding: 1px;
    color: white;
    float: right;
    border: 1px solid rgba(36, 36, 36, 0);
    background-color: rgba(12, 12, 12, 0.219);
    border-radius: 5px;
    font-size: 12px;
    transition: 0.1s ease-out;
  }
  .hex-value:hover {
    bottom: 0%;
    font-size: 14px;
    transform: rotate(-90deg);
    background-color: rgba(236, 236, 236, 0.767);
    color: rgba(0, 0, 0, 0.836);
  }
  .style_continue{
    height: 100%;
    left: 0%;
    margin: auto 0;
    position: relative;
    display: block;
    align-items: center;
  }
  
}

